.footer {
    background-color: #FFF5F6;

    &__title {
        font-family: 'Titan One';
        text-transform: uppercase;
        font-size: 18px;
        line-height: 1.17;
        text-transform: uppercase;
        color: #FFA5BA;
        max-width: 232px;
        margin-bottom: 17px;
        margin-top: 0;
    }

    &__title--big {
        font-family: 'Titan One';
        font-size: 34px;
        line-height: 1.15;
        letter-spacing: 0.04em;
        text-transform: uppercase;
        color: #D41443;
        max-width: 200px;
        margin-top: 0;
    }

    &__contact-us {
        font-family: 'DM Sans', sans-serif;
        font-style: normal;
        font-weight: 700;
        font-size: 12px;
        line-height: 1.83;
        text-align: right;
        color: #000000;
    }

    &__contact-us--phone {
        color: #000000;
    }

    &__copyright {
        border-top: 1px solid #E1E1E1;
        background-color: #FFF5F6;
    }
    &__copyright-text {
        font-family: 'DM Sans';
        font-weight: 700;
        font-size: 14px;
        line-height: 1.52;
        text-align: center;
        color: #907E82;
        margin: 22px auto 25px;
        max-width: 150px;
    }
}

@media screen and (min-width: 768px) {
    .footer {
        display: flex;
        justify-content: space-between;

        &__copyright-text {
            max-width: 100%;
            margin: 29px auto 18px;
        }
    }
}

@media screen and (min-width: 1200px) {
    .footer {

        &__copyright-text {
            margin: 9px auto 10px;
        }
    }
}